.main-prividleges-div {
  background: #150F22;
  padding: 100px 0px;

  @media (max-width: 1024px) {
    padding: 80px 0px;
  }

  @media (max-width: 768px) {
    padding: 60px 0px;
  }

  @media (max-width: 480px) {
    padding: 40px 0px;
  }
}

.main-heading-div {
  h4 {
    color: #fff;
    font-family: Urbanist;
    font-size: 12px;
    font-weight: 400;

    @media (max-width: 480px) {
      font-size: 11px;
      text-align: center;
    }
  }

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 40px;
    font-weight: 500;

    @media (max-width: 1024px) {
      font-size: 32px;
    }

    @media (max-width: 768px) {
      font-size: 28px;
      text-align: center;
    }

    @media (max-width: 480px) {
      font-size: 24px;
    }
  }
}

.other-privileges {
  display: grid;
  grid-template-columns: repeat(4, 1fr);

  @media (max-width: 1024px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 600px) {
    grid-template-columns: 1fr;
  }

  .privilege {
    border-top: 1px dashed rgba(255, 255, 255, 0.2);
    border-right: 1px dashed rgba(255, 255, 255, 0.2);
    padding: 51px 19px 19px 19px;

    @media (max-width: 480px) {
      padding: 30px 15px;
      text-align: center;
    }

    h1 {
      color: #fff;
      font-family: Urbanist;
      font-size: 22px;
      font-weight: 500;
      margin: 30px 0 20px 0;

      @media (max-width: 1024px) {
        font-size: 20px;
        margin-top: 60px;
      }

      @media (max-width: 768px) {
        font-size: 18px;
      }
    }

    p {
      color: #a6a6a6;
      font-family: Urbanist;
      font-size: 18px;
      font-weight: 400;

      @media (max-width: 1024px) {
        font-size: 16px;
      }

      @media (max-width: 768px) {
        font-size: 15px;
      }
    }
  }
}

.login-entry-point {
  position: relative;
  margin-top: 180px;
  border-radius: 24px;
  background: linear-gradient(130deg, #492fb2 -1.74%, #adadad 108.02%);
  display: flex;
  align-items: center;

  @media (max-width: 1024px) {
    flex-direction: column;
    margin-top: 100px;
    padding: 0px !important;
    overflow: hidden;
  }

  @media (max-width: 768px) {
    margin-top: 80px;
  }

  @media (max-width: 480px) {
    margin-top: 60px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 40%;
    height: 100%;
    background-image: url('../../../../assets/images/rings-login.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: left top;
    border-top-left-radius: 24px;
    border-bottom-left-radius: 24px;
    z-index: 1;

    @media (max-width: 1024px) {
      width: 100%;
      height: 200px;
      background-position: center;
      border-radius: 24px 24px 0 0;
    }
  }

  .left-cont-div {
    position: relative;
    z-index: 2;
    padding: 40px;
    flex: 1;

    @media (max-width: 768px) {
        width: 100% !important;
      padding: 20px;
      text-align: center;
    }

    h3 {
      color: #fff;
      font-family: Urbanist;
      font-size: 30px;
      font-weight: 500;
      line-height: 36px;

      @media (max-width: 1024px) {
        font-size: 26px;
      }

      @media (max-width: 768px) {
        font-size: 22px;
      }
    }

    p {
      color: #fff;
      font-family: Urbanist;
      font-size: 18px;
      font-weight: 400;

      @media (max-width: 1024px) {
        font-size: 16px;
      }

      @media (max-width: 768px) {
        font-size: 15px;
      }
    }
  }

  .right-image-divlogin{
              @media (max-width: 1024px) {
        margin-top: 30%;
        width: 100% !important;
      }
  }
}

.btn-login {
  padding: 8px 16px;
  border-radius: 8px;
  background: #fff;
  color: #063787;
  font-family: Urbanist;
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 30px;

  @media (max-width: 480px) {
    justify-content: center;
    width: 100%;
  }
}

.floating-div {
  padding: 8px 12px;
  border-radius: 58px;
  background: #fff;
  box-shadow: 0 10px 15px -3px rgba(6, 48, 117, 0.1);
  display: flex;
  align-items: center;
  gap: 10px;
  color: #111827;
  font-family: Urbanist;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  position: absolute;

  @media (max-width: 768px) {
    font-size: 13px;
    padding: 6px 10px;
  }

  @media (max-width: 480px) {
    font-size: 12px;
    gap: 6px;
  }
}

.position1 {
  left: 0px;

  @media (max-width: 768px) {
    left: 10px;
  }
}

.position2 {
  bottom: 20%;
  right: 10px;

  @media (max-width: 768px) {
    right: 10px;
    bottom: 5%;
  }
}
